.h-1
{
    font-size:2rem;
    font-weight: bold;
    color: rgb(124, 124, 124);
    padding-top: 1rem;

    font-family: 'Poppins', sans-serif;; text-align: center; text-transform: uppercase;
}
.s-1
{
    height: 50px;
    text-align: center;
    background-color: #e6f5ff;
}


.project-layout
{
   display: flex;
   flex-direction: column;
   position: relative;
   margin-left: 0px;
   margin-right: 0px;
   padding: 1rem;
  
   
}

.p-heading-1
{
    font-size: 30px;
    word-spacing: 0px;
    font-weight: 800;
    font-family: 'Poppins', sans-serif;
    padding-left: 10px;
    color: #000000;
    text-align: left;



}

.c-1
{
   display: grid;
   grid-template-columns:300px 1fr 200px;
}
.desc
{
    text-align: justify;
    font-size: 20px;
    height: 100%;
    font-family: Helvetica;
    color: #3f413f;
    word-spacing: 5px;
    line-height: 31px;
    text-align: left;
    padding-top: 1rem;
}
    


.c-2
{
    display: grid;
    grid-template-rows: 70px 1fr;
}


.container {

    padding-right:1.01rem ;
    width: 110%;
    text-align: center;
    font-size: 0;
    font-family: Helvetica;
    font-weight: 400;

    text-align: right;

}

h2.title {
    position: relative;
   

    color: #222222;
}

h2.title::after {
    position: absolute;
    content: "";
    width: 50px;
    height: 3px;
    left: calc(50% - 25px);
    bottom: 0;
    background: #222222;
}

p {
    position: relative;

    font-size: 16px;
    color: #222222;
}

.btn {
    position: relative;
    display: inline-block;

    padding: 12px 27px;
    text-align: center;
    font-size: 16px;
    letter-spacing: 1px;
    text-decoration: none;
    color: #999999;
    background: #ffffff;
    border: 3px solid #999999;
    cursor: pointer;
    transition: ease-out 0.5s;
    -webkit-transition: ease-out 0.5s;
    -moz-transition: ease-out 0.5s;
}


.btn.btn-border-2::after,
.btn.btn-border-2::before {
    position: absolute;
    content: "";
    width: 0;
    height: 0;
    transition: .5s;
}

.btn.btn-border-2::after {
    top: -3px;
    left: -3px;
    border-top: 3px solid transparent;
    border-left: 3px solid transparent;
}

.btn.btn-border-2::before {
    bottom: -3px;
    right: -3px;
    border-bottom: 3px solid transparent;
    border-right: 3px solid transparent;
}

.btn.btn-border-2:hover {
    color: #222222;
}

.btn.btn-border-2:hover::after,
.btn.btn-border-2:hover::before {
    width: calc(50% + 3px);
    height: calc(50% + 3px);
    border-color: #222222;
}


.project-image
{
    height: 90%;
    padding: 2rem;
    padding-top: 2rem;
    margin-left: auto;
    margin-right: auto;

    
}
.p-desc
{
    display: grid;
    grid-template-columns: 0.2fr 5fr 1fr;
}
.image {
    padding: 2rem;
    padding-left: 1rem;
    padding-right: 1rem;
    display: grid;
    grid-template-columns: 0.5fr 3fr 1fr;
    position: relative;
    
    cursor: pointer;

  }

  .project-image{
   

    transition: transform 0.5s ease-in-out;
  }

  .image:hover .project-image {
    transform: scale(1.05);
  }


  .projects-heading
  {
    float: left;
    font-size: 35px;
  }
.projects-heading-style
{
   
    font-size: 35px;
    word-spacing: 0px;
    font-weight: 800;
    font-family: 'Poppins', sans-serif;
    float: left;
    color: #3498db;
    padding: 2rem;
    
}
.all-section
{
    display: grid;
    grid-template-rows: 110px 1fr;
    padding-bottom: 5rem;
}
.m-image
{
    margin: auto;
}
.projects-heading-style
{
    margin-left: 290px;
}
  @media screen and (max-width: 1541px) {
    .p-heading-1{
        font-size: 2rem;
    }
    .projects-heading-style
    {
        margin: auto;
    }
 
    .middle
    {   
        padding-top:1rem ;
    }
    .c-1{
        display: grid;
        grid-template-columns: 10px 1fr 10px;
    }
  
    .p-desc
    {
        display: grid;
        grid-template-columns: 0.1fr 1fr 0.1fr;
    }
}
  

@media screen and (max-width: 1146px) {
    .project-image{
        height: 80%;
    }
    .project-layout
    {
        padding-left: 0%;
        padding-right: 0%;
    }
    
}
  


@media screen and (max-width: 947px) {


.project-image
{
    padding: 0px;
    height: 100%;
}
.p-heading-1
{
    font-size: 26px;
}
.image
{
 display: grid;
 grid-template-columns: 10px 1fr 10px;
}


.projects-heading-style
{
    padding-left: 10px;
}
}
  

@media screen and (max-width: 695px) {
  
   
    
  
