.sticky-social-bar {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
   background-color: #22142b;
   
    padding: 10px;

    z-index: 999; /* Ensure the bar is above other elements */
    transition: all 0.3s ease; /* Add smooth transition effect */
    display: flex;
    justify-content: space-between;
    align-items: center;
    height:50px;
  }
  
  .collapsed {
    background-color: transparent; /* Set background to transparent when collapsed */
    box-shadow: none;
  }
  
  .collapsed .social-icons {
    display: none;
  }
  
  .social-icons ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
  }
  
  .social-icons li {
    margin-right: 20px; /* Increase the margin for more spacing between icons */
  }
  
  .social-icons a {
    display: flex;
    align-items: center;
    color: #f3f3f3; /* Add your desired icon color */
    transition: color 0.3s ease; /* Add smooth transition for icon color change on hover */
  }
  
  .social-icons i {
    font-size: 28px; /* Increase the icon size as needed */
  }
  

  .toggle-button {
    background-color: #22142b;; /* Add your desired button color */
    color: #fff; /* Add your desired button text color */
    border: none;
    padding: 10px 16px; /* Adjust padding for a more spacious look */
    cursor: pointer;
    font-size: 16px; /* Increase font size for better visibility */
    border-radius: 4px; /* Add border-radius for rounded corners */
    transition: background-color 0.3s ease; /* Add smooth transition for button color change on hover */
    margin-top: 10px; /* Add margin to separate from social icons */
  }
  
  .toggle-button:hover {
    background-color: #2980b9; /* Add your desired hover color */
  }
  
  .sticky-social-bar.collapsed .toggle-button {
    background-color: #1c6991; /* Add a different color when social icons are hidden */
  }
  
  /* Adjust the color and styles based on your design preferences */
  





  

  
  .social-icons a:hover {
    color: #1c6991; /* Add your desired icon color on hover */
  }

