/* Simple button styling */
.button {
  display: inline-block;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  cursor: pointer;

  border: 2px solid #207ce5; /* Border color */
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s, transform 0.3s, box-shadow 0.3s;

  color: #207ce5; /* Text color */
  background-color: #fff; /* Background color */
  
  /* Add a subtle box shadow */
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

/* Hover effect */
.button:hover {
  background-color: #207ce5; /* Change background color on hover */
  color: #fff; /* Change text color on hover */
  box-shadow: 0px 8px 12px rgba(0, 0, 0, 0.2); /* Increase box shadow on hover */
  transform: translateY(-2px); /* Move the button up slightly on hover */
}

/* Active/Pressed effect */
.button:active {
  transform: translateY(2px); /* Simulate a pressed effect by moving the button down slightly */

  font-family: Helvetica;
}




