
.main
{
  background-color:  #e6f5ff;
}
.publications-container {
    max-width: 800px;
    margin: 0 auto;
    font-family: Helvetica;

  }
  
  .publications {
    margin-bottom: 20px;
    padding-top:1rem;    
    max-width: 1500px;
    padding-bottom: 4rem;
  }
  .block
  {
    height: max-content;
    width: 100%;
    
    padding: 2rem;
    padding-bottom: 1.1rem;
    font-size: 18px;
    font-family: Helvetica;

  text-align: justify;
  cursor: pointer;
  transition: transform 0.3s ease-out;

  
	font-weight: 500;
	-webkit-font-smoothing: antialiased;
  color: #525252;

  
  }

.app
{
      background-color:  #e6f5ff;
}
.main
{
  display: grid;
  grid-template-columns: 1.2fr 6fr 1.2fr;
}
.block:hover 
{ transform: scale(1.03);
  color: #060607;

  }
 .border-1
 {  border-bottom:1px solid rgb(169, 214, 231) ;
  transition: border 0.3s ease, color 0.3s cubic-bezier(0.68, -0.55, 0.27, 1.55); /* Apply transitions to border and color */
 }

 .border-1:hover
 {border-bottom: 1px solid #010405; /* Border on hover */
  
 }


 .box
 {
  display: grid;
  grid-template-rows: 70px 1fr;
 }
 .text-heading
 {font-size: 30px;
  padding: 1rem;
  font-family: Helvetica;
  float: left;
  padding-right :0rem;
  color:#3498db;
  text-align: center;
  font-weight: 600;


  







 }
 .social-menu
 {
  margin-left:15%;
  position:fixed;
  margin-top: 10%;
 }

 .block1
 {top: 10%;
  font-size: 30px;
  font-weight: 600;
  color: #171f20;
  text-align: left;
 }
 .citation
 {padding-bottom: 0.5rem;
  color: #676865; 
  text-align: justify;
  padding-left: 2rem;
 
  font-size: 13px;
 }



.side
{
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
}
.icon 
{
  font-size: 2rem;
  color: rgb(151, 188, 212);
 
}
.Heading 
{
  display: grid;
  grid-template-columns: 200px 5fr;
}
.i-1
{font-size: 1.8rem;
  margin-top: 1rem;
  color: #3498db;
  cursor: pointer;
  transition: color 0.3s ease;

}
.i-1:hover
{
color: #d87a7a;
}

.border-1 li
{font-family: Helvetica;}

/*Breakpoints*/







@media screen and (max-width: 1403px) {
  .main

  {
    display: grid;
    grid-template-columns: 50px 1fr 50px;
  }

  
  }
  





@media screen and (max-width: 947px) {
  .main

  {
    display: grid;
    grid-template-columns: 0px 1fr 0px;
  }
  .block
  {
    font-size: 15px;
    text-align: justify;
   
   
	font-weight: 500;
	-webkit-font-smoothing: antialiased;

  }


}



@media screen and (min-width: 2500px) {
  .main
  {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

