.slider
{position: relative;
    display: inline-block;
    
   


}

.slider-images
{width: 1140px;
height: 210px;
border-radius: 10px;
-webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.38);
-moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.38);
box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.38);


}



.right-arrow
{ position: absolute;
    
    top: 50%;
    right:10px;
    font-size: 2rem;
    color: #f8f8f8;
    z-index: 10;
    cursor: pointer;
    user-select: none;
}
.left-arrow
{ position: absolute;
    
    top: 50%;
    left:10px;
    font-size: 2rem;
    color: #dbdbdb;
    z-index: 10;
    cursor: pointer;
    user-select: none;
}

.blurr
{width: 100%;
    height: 100%;
   background-color: rgba(70, 68, 68, 0.5);
   background-image: url('../Assets/Header-Background.png');
   background-repeat: no-repeat;
   background-size: 100% 100%;
 

}


.slider1{
    height:100%;
    width: 100%;
    position: relative;
  
    background-color: rgba(3, 3, 3, 0.1); 
    backdrop-filter: blur(0px); 
  


}

 
   
  
.slide-active
{
    padding: 3rem;
    padding-left: 0rem;
    padding-right: 0rem;


}

/*
  Breakpoints
*/



@media screen and (max-width: 1250px) {
    .slider-images{
        width: 814px;
        height: 180px;
      
    }
    
  }

  
@media screen and (max-width: 890px) {
    .slider-images{
        width: 650px;
        height: 150px;
      
    }
    
  }

  @media screen and (max-width: 680px) {
    .slider-images{
        width: 600px;
        height: 140px;
      
    }
    
  }

  @media screen and (max-width: 630px) {
    .slider-images{
        width: 550px;
        height: 120px;
    }
    
  }
  @media screen and (max-width: 580px) {
    .slider-images{
        width: 500px;
        height: 110px;
    }
    
  }

  @media screen and (max-width: 520px) {
    .slider-images{
        width: 450px;
        height: 110px;
    }
    
  }

  @media screen and (max-width: 450px) {
    .slider-images{
        width: 350px;
        height: 100px;
    }
    
  }


  
