/* Contact.css */

.contact-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 42vh;
}

.contact-card {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 600px;
  padding: 40px;
  border-radius: 15px;
  background-color: #ffffff; /* Light color background */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle box shadow */
  text-align: left;
  transition: transform 0.3s ease-in-out;
  overflow: hidden; /* Hide overflow to prevent circular overlay from protruding */
}

.contact-card:hover {
  transform: scale(1.05); /* Scale up on hover */
}

.contact-card::before {
  content: '';
  position: absolute;
  top: 100%; /* Set the starting point to bottom */
  left: 0;
  width: 100%;
  height: 100%;

  background-color: #404344; /* Initial circular background color */
  transform: scale(0); /* Initially scaled down */
  transform-origin: center bottom; /* Set the origin to the center-bottom */
  transition: transform 0.3s ease-in-out, background-color 0.3s ease-in-out;
  z-index: -1; /* Place the overlay behind the content */
}

.contact-card:hover::before {
  transform: scale(2); /* Scale up on hover to cover the entire card */
  background-color: #d3dae0;
}

.contact-icon {
  margin-right: 20px;
}

.contact-content {
  flex: 1;
}

.contact-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #333; /* Text color */
  margin-bottom: 20px;
  transition: color 0.3s ease-in-out; /* Color change on hover */
}

.contact-card:hover .contact-title {
  color: #2072b8;
}

.contact-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 20px;
  transition: transform 0.3s ease-in-out, color 0.3s ease-in-out; /* Scale and color change on hover */
}

.contact-info:hover {
  transform: scale(1.1);
  color: #2072b8;
}

.contact-text {
  color: #555; /* Text color */
  font-size: 1.2rem;
  text-align: center; /* Center the text */
}

.contact-text a {
  color: #3498db; /* Link color */
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}

.contact-text a:hover {
  color: #2072b8; /* Hovered link color */
}
 /* Breakpoints */
 @media screen and (max-width:300px) {
    .contact-card
    {
        padding: 20px;
    }

	
  }