.introduction
{
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 500px 2fr 100px;
    margin-top: 5rem; 
    overflow: hidden;
    background-color:#e6f5ff;
}
.profile_pic
{
    height: 260px;
    width: 230px;
    border:solid 1px black;
    margin-top: 1rem;
  
    
   float: right;
}

.intro
{

    display: grid;
    grid-template-rows: 50px 1fr;
}
.heading
{ padding-left: 5rem;
    float: left;
    font-size: 35px;
    font-weight: bold;
   font-family: 'Poppins', sans-serif;
    color: #3498db;
    

}
.content-contain
{
    height: 100%;
    padding:5rem ;
    padding-bottom:0rem ;
    padding-top: 1rem;
    text-align: justify;
    font-family: Helvetica;
    text-justify: newspaper;
  


}
.contents
{
    font-size: 20px;
    height: 100%;
    
    font-family: Helvetica;
    color: #3f413f;
    word-spacing: 5px;
    line-height: 31px;
    text-align: left;
    
   
}



.new_introduction
{
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: 290px 2fr;
    margin-top: 3rem; 
    overflow: hidden;
    background-color:#e6f5ff;
}



.new_img
{
    margin-left: auto;
    margin-right: auto;
   

}

.new_profile_pic
{
    height: 250px;
    width: 220px;
    border:solid 1px black;
}

.new_content-contain
{
    padding: 2rem;
   
    text-align: justify;
    word-break: break-all;

    font-family:Helvetica;
   


    color: #000000;
    font-family: Helvetica;
    font-size: 16px;
    font-weight: 500;
    -webkit-font-smoothing: antialiased;

    
}



.b-v
{   margin-top:1.5rem ;
    height: 1px;
    width: 100%;
    border-top: 1px solid #c3d4f8;;
    padding-bottom: 4rem;
}

.scholar
{font-size: 1.6rem;}



.add1
{
    display:grid;
grid-template-columns: 260px 2fr 100px;}







.Academics
{
    padding-bottom: 1rem;
    display: grid;
    grid-template-rows: 60px 1fr;

}

.time-period

{
    font-weight: bolder;

}
.text
{
    text-align: justify;
    font-size: 20px;
    padding: 0.5rem;
    padding-right: 0px;

   
    font-family: Helvetica;
    color: #3f413f;
    word-spacing: 5px;
    line-height: 31px;
    text-align: left;
   

   
}
.A-2
{
    padding-right: 2rem;
}

#h-2
{padding-left: 0%;
}
.add2
{
    display: grid;
    grid-template-columns: 260px 2fr 100px;
   
}
.Awards-new-section
{
    display: grid;
    grid-template-rows: 60px 1fr;

}



.Academics
{
    padding-top: 1rem;
}















